/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #e3e3e3;
	font-family: "Didact Gothic", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 1.4rem;
	line-height: 1.8;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
}

h1, h2, h3 {
	font-family: "Kodchasan", sans-serif;
}

h1 {
	font-weight: 600;
	font-size: 2.5rem;
}

h2 {
	font-style: italic;
	font-size: 2rem;
}

p {
	margin: 0 auto;
	padding: 0 1.5rem;
	max-width: 43rem;
	font-size: 1.4rem;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #0f0f0f;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #0f0f0f;
}

hr {
	background-color: #0f0f0f;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 0em;
}

ul {
	list-style: none;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #e3e3e3;
	text-decoration: none;
}
a:visited {
	color: #e3e3e3;
}
a:hover, a:focus, a:active {
	color: #7FE5F0;
}
a:focus {
	outline: thin dotted;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: none;
	border-radius: 3px;
	background: #0f0f0f;
	color: #000;
	line-height: 1;
	padding: 0.5em 1em 0.5em 1em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #7FE5F0;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #C13535;
}

body .wp-block-post-excerpt__more-link,
.project-links {
	padding: 0.5rem 1rem;
	border-radius: 2rem;
	background-color: #C13535;
}

#project-link-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
#project-link-container .project-links {
	text-align: center;
	width: 12rem;
	margin: 0.75rem;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #2b2b2b;
	border: 1px solid #C13535;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #C13535;
}

select {
	border: 1px solid #C13535;
}

textarea {
	width: 100%;
}

body .wpforms-container-full {
	padding: 1.5rem;
	max-width: 32rem;
}
body .wpforms-container-full .wpforms-form button[type=submit] {
	background-color: #C13535;
	border: none;
	padding: 0.5rem 1rem;
	border-radius: 2rem;
}
body .wpforms-container-full .wpforms-form button[type=submit]:hover {
	background-color: #7FE5F0;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: flex;
	flex-wrap: nowrap;
	justify-content: flex-end;
	background-color: #fff;
	border-bottom: 6px solid #C13535;
	box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 99998;
}
.main-navigation .nav-menu {
	align-items: center;
	padding-right: 2rem;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	align-items: baseline;
}
.main-navigation li {
	position: relative;
	padding: 1rem;
}
.main-navigation a {
	display: block;
	text-decoration: none;
	color: #0f0f0f;
	font-size: 1.5rem;
	padding: 0.25rem 0 0 2rem;
}
@media screen and (min-width: 51em) {
	.main-navigation a {
		padding: 0.75rem 0.25rem;
	}
}
.main-navigation a:hover {
	color: #C13535;
}
@media screen and (max-width: 51em) {
	.main-navigation {
		display: inline;
		max-height: 100vh;
	}
	.main-navigation li {
		padding: 0.5rem;
	}
	.main-navigation a {
		text-align: center;
	}
}

.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}
@media screen and (min-width: 51em) {
	.menu-toggle,
.main-navigation.toggled ul {
		display: flex;
	}
}

.menu-toggle {
	background-color: #fff;
	font-size: 2.25rem;
}

@media screen and (min-width: 51em) {
	.menu-toggle {
		display: none;
	}

	.main-navigation ul {
		display: flex;
	}
}
.comment-navigation,
.posts-navigation,
.post-navigation {
	padding: 0 1rem;
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

.social-icon a svg {
	width: 3rem;
	height: 3rem;
	margin-top: 1rem;
}
.social-icon a svg path {
	fill: #7FE5F0;
}
@media screen and (max-width: 51em) {
	.social-icon a svg {
		width: 2rem;
		height: 2rem;
		display: inline;
	}
}
.social-icon a:hover svg path {
	fill: #C13535;
}

/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.post,
.page {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

body .wp-block-pullquote blockquote {
	display: block;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	border-top: 4px solid #7FE5F0;
	border-bottom: 4px solid #7FE5F0;
	max-width: 30rem;
}

section.wp-block-query.project-card ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
section.wp-block-query.project-card ul a {
	color: #0f0f0f;
}
section.wp-block-query.project-card .wp-block-post-template li {
	padding: 0 1rem 10rem 1rem;
	margin: 1rem;
	background-color: #fff;
	color: #000;
	max-width: 31rem;
	border-radius: 0.5rem;
	box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
	position: relative;
}
section.wp-block-query.project-card .wp-block-post-template li .project-excerpt.wp-block-post-excerpt {
	position: absolute;
	bottom: 1rem;
	left: 0;
	right: 0;
	margin-top: 4rem;
}
section.wp-block-query.project-card .wp-block-post-excerpt__more-text {
	text-align: center;
}
section.wp-block-query.project-card .project-title {
	text-align: center;
}

section.wp-block-group.blog {
	background-color: #fff;
	color: #000;
	padding: 1rem 0 2rem 0;
}
section.wp-block-group.blog ul a {
	color: #0f0f0f;
}
section.wp-block-group.blog .wp-block-post-template li {
	padding: 1rem;
	margin: 2rem auto;
	background-color: #fafafa;
	color: #000;
	max-width: 40rem;
	border-radius: 1rem;
	box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
}
section.wp-block-group.blog .blog-title {
	text-align: center;
}
section.wp-block-group.blog .blog-categories a {
	font-size: 1rem;
	padding: 0.25rem;
	border-radius: 0.5rem;
	background-color: #7FE5F0;
}
section.wp-block-group.blog .read-more {
	color: #C13535;
}

body .page-title {
	color: #e3e3e3;
	text-align: center;
	padding: 6.5rem 1rem 1rem 1rem;
}
@media screen and (min-width: 51em) {
	body .page-title {
		padding-top: 10rem;
	}
}

.tech-stack h2 {
	text-align: center;
}
.tech-stack li svg {
	max-width: 5rem;
}
.tech-stack li svg path {
	fill: #7FE5F0;
}
.tech-stack ul {
	max-width: 50rem;
	padding: 1rem;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	margin: 0 auto;
}

.gist {
	padding: 1rem;
	max-width: 50rem;
	margin: 0 auto;
}

.site-footer {
	padding: 2rem;
	text-align: center;
}

body .error-404 {
	margin: 0 auto;
	padding: 1rem;
	max-width: 50rem;
}

.privacy-policy {
	padding-top: 6.5rem;
}
@media screen and (min-width: 51em) {
	.privacy-policy {
		padding-top: 10rem;
	}
}
.privacy-policy h1, .privacy-policy h2 {
	text-align: center;
}
.privacy-policy p {
	overflow-wrap: break-word;
}

/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

body .wp-block-group.hero-group {
	background-color: #fff;
	padding: 4rem 2rem 0 2rem;
}
@media screen and (min-width: 53em) {
	body .wp-block-group.hero-group {
		padding: 10rem 2rem 0;
	}
}
body .wp-block-group.hero-group .wp-block-group__inner-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
@media screen and (min-width: 53em) {
	body .wp-block-group.hero-group .wp-block-group__inner-container {
		flex-direction: row;
		max-width: 50rem;
		margin: 0 auto;
	}
}
body .wp-block-group.hero-group .logomark {
	max-width: 34rem;
	margin: 0 auto;
	padding-top: 1rem;
}
body .wp-block-group.hero-group .logomark figure {
	max-width: 25rem;
}
body .wp-block-group.hero-group .logo-wordmark {
	max-width: 24rem;
	margin: 0 auto;
	padding-top: 2rem;
}
@media screen and (min-width: 53em) {
	body .wp-block-group.hero-group .logo-wordmark {
		padding-top: 8rem;
	}
}

.wp-block-image:not(.is-style-rounded) img.wp-image-11 {
	clip-path: polygon(39% 5%, 93% 12%, 95% 55%, 78% 88%, 0 95%, 0 11%);
}

.wp-block-image:not(.is-style-rounded).scroll-icon {
	background-color: #fff;
}

.wp-block-pullquote {
	margin-bottom: 0;
}

.wp-block-image .aligncenter {
	margin-bottom: 0;
	padding-top: 3rem;
}

body .wp-block-image .aligncenter > figcaption {
	font-size: 0.9rem;
	transform: rotate(-3.8deg) translateY(-2.5rem);
	padding-left: 1rem;
}
@media screen and (min-width: 50em) {
	body .wp-block-image .aligncenter > figcaption {
		transform: rotate(-90deg) translate(322px, -310px);
	}
}

#project-feat-img {
	display: block;
	margin-left: auto;
	margin-right: auto;
	padding: 1rem;
}
@media screen and (min-width: 50em) {
	#project-feat-img {
		max-width: 65%;
	}
}

.sub-img-container {
	padding: 1rem;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.sub-img-container img {
	padding: 1rem;
	border-radius: 2rem;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/
/* Jetpack infinite scroll
--------------------------------------------- */
/* Hide the Posts Navigation and the Footer when Infinite Scroll is in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	display: none;
}

/* Re-display the Theme Footer when Infinite Scroll has reached its end. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
/* Accessibility
--------------------------------------------- */
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
}
.screen-reader-text:focus {
	background-color: #C13535;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #7FE5F0;
	display: block;
	font-size: 0.875rem;
	font-weight: 700;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
	outline: 0;
}

/* Alignments
--------------------------------------------- */
.alignleft {
	/*rtl:ignore*/
	float: left;
	/*rtl:ignore*/
	margin-right: 1.5em;
	margin-bottom: 1.5em;
}

.alignright {
	/*rtl:ignore*/
	float: right;
	/*rtl:ignore*/
	margin-left: 1.5em;
	margin-bottom: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 1.5em;
}